import { NavLink } from "react-router-dom";

const NavBarMobile = () => {
    return (
        <div className="fixed bottom-4 md:hidden w-full flex justify-center" >
            <div className="px-7 bg-white shadow-lg rounded-2xl">
                <div className="flex">
                    <div className="flex-1 group">
                        <NavLink to="/d-bot-info" className="flex items-end justify-center text-center mx-auto px-4 pt-2 w-full text-gray-400 group-hover:text-blue-700">
                            <span className="block px-1 pt-1 pb-1">
                                <i className="far fa-info-circle text-2xl pt-1 mb-1 block" />
                                <span className="block text-xs pb-2">Info</span>
                                <span className="block w-10 mx-auto h-1 group-hover:bg-blue-700 rounded-full" />
                            </span>
                        </NavLink>
                    </div>
                    <div className="flex-1 group">
                        <NavLink to="/d-bot-info/faqs" className="flex items-end justify-center text-center mx-auto px-4 pt-2 w-full text-gray-400 group-hover:text-blue-700">
                            <span className="block px-1 pt-1 pb-1">
                                <i className="far fa-question-circle text-2xl pt-1 mb-1 block" />
                                <span className="block text-xs pb-2">Faq</span>
                                <span className="block w-10 mx-auto h-1 group-hover:bg-blue-700 rounded-full" />
                            </span>
                        </NavLink>
                    </div>
                    <div className="flex-1 group">
                        <NavLink to="/d-bot-info/team" className="flex items-end justify-center text-center mx-auto px-4 pt-2 w-full text-gray-400 group-hover:text-blue-700">
                            <span className="block px-1 pt-1 pb-1">
                                <i className="far fa-user-circle text-2xl pt-1 mb-1 block" />
                                <span className="block text-xs pb-2">Equipo</span>
                                <span className="block w-10 mx-auto h-1 group-hover:bg-blue-700 rounded-full" />
                            </span>
                        </NavLink>
                    </div>
                </div>
            </div>

        </div>
    );
}

export default NavBarMobile;
