import { NavLink } from "react-router-dom";

const Navbar = () => {
    return (
        <nav className="flex justify-end py-3 px-5 navbar relative bg-blue-800">

            <div className="hidden md:flex items-center">
                <NavLink exact to="/d-bot-info" activeClassName="active">Info</NavLink>
                <NavLink exact to="/d-bot-info/faqs" activeClassName="active">Preguntas frecuentes</NavLink>
                <NavLink exact to="/d-bot-info/team" activeClassName="active">Equipo</NavLink>
            </div>

        </nav>
    );
}

export default Navbar;
