import faqData from './faq.json'
import { useState } from 'react'

const FaqListItem = ({ answer = 'ans', question = 'quest' }) => {

    const [isVisible, setIsVisible] = useState(false);

    const handlerclick = () => {
        setIsVisible(!isVisible)
    }

    return (
        <div className="mb-2 shadow-md rounded-md bg-white">
            <button onClick={handlerclick} className="flex items-center justify-between w-full p-4 active:bg-blue-700" >
                <h4 className="font-semibold text-left">{question}</h4>
                <svg className="w-5 h-5 text-gray-500" fill="none" strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} viewBox="0 0 24 24" stroke="currentColor">
                    <path d="M19 9l-7 7-7-7" />
                </svg>
            </button>
            <div className={`px-4 pb-4 transition-all duration-200 ${(!isVisible) && 'hidden'}`}>
                <p>{answer}</p>
            </div>
        </div >
    )
}

const FaqList = ({ faqs = [] }) => {

    const fl = faqs.map((el, index) => <FaqListItem answer={el.answer} question={el.question} key={index} />)

    return (
        <div>
            {fl}
        </div>
    )
}

const Faq = () => {
    const [faqs, setFaqs] = useState(faqData);

    const handlerSearch = (val = '*') => {
        const regex = new RegExp(val, 'gi');
        if (val === '') { val = '*' }
        const findFaqs = faqData.filter((el) => (regex.test(el.question)) && el);
        setFaqs(findFaqs)
    };

    return (
        <div className="flex flex-col md:flex-row h-full mx-auto justify-center">
            <div>
                <div className="flex flex-col rounded-2xl w-full bg-white shadow-md p-4 text-left md:w-4/12 min-w-max">
                    <span className="text-2xl ">¿Cual es su pregunta?</span>
                    <span className="text-xs ml-1">Recopilación de las preguntas más comunes</span>
                    <div className="mb-3 space-y-2 text-xs my-1">
                        <input
                            placeholder="Escriba su pregunta..."
                            className="appearance-none block w-full bg-gray-200 text-gray-800 rounded-lg h-10 px-4 hover:ring border-gray-400 focus:outline-none active:ring active:border-blue-700 transition-all duration-150"
                            onChange={(e) => handlerSearch(e.target.value)}
                            required="required"
                            type="text"
                        />
                    </div>
                </div>
            </div>
            <div className="md-8/12 px-4 mt-4 sm:mt-0 overflow-y-auto scroll h-full max-w-lg">
                <FaqList faqs={faqs} />
            </div>
        </div>
    );
}

export default Faq;
