import Routes from "./routes/routes";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import Error404 from "./pages/Error404";

// < className="bg-trueGray-200 min-h-screen" />

function App() {
  return (

    <Router>
      <Switch>
        <Route exact path="/">
          <Redirect to="/d-bot-info" />
        </Route>
        <Route path="/d-bot-info" component={Routes} />
        <Route path="/404" component={Error404} />
        <Route path="*">
          <Redirect to="/404" />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
